@import './varibles.scss';

.first-block {
    width: 100%;
    height: 850px;
    background-image: url(../img/background.svg);
    background-repeat: no-repeat;
    background-size: cover;
}

.first-block__content {
    outline: 1px solid red;
    margin: auto;
    padding-top: 70px;
    width: 100%;
    max-width: $content-weight;

    & header {
        display: flex;
        justify-content: space-between;
        padding: 0px 15px 0px 15px;
    }
}

.first-block__menu {
    ul {
        display: flex;
        list-style: none;
        margin: 0;
        padding-left: 0;
        font-weight: 300;
        
        & li {
            padding: 0px 40px 0px 40px;

            &:first-child {
                padding-left: 0px;;
            }
            &:last-child {
                padding-right: 0px;;
            }
        }

        & a {
            color: #fff;
            text-decoration: none;
            font-size: 16px;
            text-transform: uppercase;
        }
      }
}

.first-block__article {
    & h1 {
        .Arcane_UI {
            font-size: 90px;
            font-weight: bold;
            text-transform: uppercase;
            line-height: 3.194;
          }
          
    }

    & p {
        font-weight: 300;
    }
}